.products .card {
    position: relative;
    width: 400px;
    height: auto;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 5px 10px rgba(0,0,0,.8);
    background: green;
}

.products .card:before {
    content: '';
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    box-sizing: border-box;
    transition: 0.5s;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    opacity: 0;
    z-index: 1;
}

.products .card:hover:before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    opacity: 1;
}

.products .card:after {
    content: '';
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    border-left: 1px solid white;
    border-right: 1px solid white;
    box-sizing: border-box;
    transition: 0.5s;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    opacity: 0;
    z-index: 1;
}

.products .card:hover:after {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    opacity: 1;
}

.products .card .imgCard {
    position: relative;
}

.products .card .imgCard img {
    width: 100%;
    transition: 0.5s;
}

.products .card:hover .imgCard img {
    opacity: .2;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.products .card .content {
    position: absolute;
    width: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 2;
}

.products .card .content h1 {
    color: white;
    transition: 0.5s;
    text-align: center;
    /* padding-top: 50px; */
    font-size: 40px;
    padding-bottom: 40px;
    /* transform: translateY(-70px); */
    opacity: 0;
    visibility: hidden;
}

.products .card:hover .content h1
    
    {
        opacity: 1;
        visibility: visible;
        /* transform: translateY(0); */
        transition-delay: 0.2s;
    } 
 
